.userinfo {
  position: absolute;
  top: 0px;
  width: 100%;
  max-width: 600px;
  height: 100%;
  background-color: white;
  .title {
    font-size: 25px;
    color: #3a98f9;
    font-weight: bold;
    width: 100%;
    margin: 30px 0px 30px 0px;
    text-align: center;
  }
  .choice-container {
    background-color: white;
    width: 100%;
    border-top: 10px solid #f5f3f3;
    padding-bottom: 10px;
    .choice-title {
      padding-top: 10px;
      width: 90%;
      margin: 0px auto 15px;
      font-size: 18px;
    }
    .choice {
      width: 90%;
      border: 1px solid #ccc;
      margin: 0px auto 15px;
      border-radius: 4px;
      box-sizing: border-box;
    }
    .next-btn {
      width: 90%;
      margin: 0px auto 15px;
      padding: 10px 0px;
    }
  }
}
